import React, { Fragment } from "react"

const MfrSelection = props => {
  const { partyTable, setCurrentSU, setCurrentMF, setCurrentSUName, setCurrentMFName, selectingSupplier, setSelectingSupplier, setMFRsearch, mfrSearch, getMCtable, setEditMode } = props

  const handleClick = (su, mf, su_name, mf_name) => {
    setCurrentSU(su)
    setCurrentMF(mf)
    setCurrentSUName(su_name)
    setCurrentMFName(mf_name)
    setSelectingSupplier(false)
    getMCtable(su, mf)
    setEditMode(false)
  }

  const handleMFRsearch = event => {
    const { value } = event.target
    setMFRsearch(value.toUpperCase())
  }

  return (
    // present the table
    <Fragment>
      {partyTable && selectingSupplier ? (
        <div>
          <div className="mfrSelection_header"> Select a manufacturer below:</div>
          <div className="mfrSelection_input">
            <input type="text" id="mfrSearch" name="mfrSearch" value={mfrSearch || ""} justify="left" onChange={handleMFRsearch} style={{ textTransform: "uppercase" }} placeholder="Type to search" />
          </div>
        </div>
      ) : null}

      {partyTable && selectingSupplier
        ? partyTable
            .filter(y => mfrSearch.length === 0 || "".concat(" ", y.SU_NAME, " ", y.SU, " ", y.MF, " ", y.MF_NAME).search(mfrSearch) > 0)
            .map(x => (
              <div
                className="mfrSelection"
                key={x.SU.concat(x.MF)}
                onClick={() => {
                  handleClick(x.SU, x.MF, x.SU_NAME, x.MF_NAME)
                }}
              >
                <div className="mfrSelectionSU">
                  {x.SU_NAME} ({x.SU}){" "}
                </div>
                <div className="mfrSelectionMF">
                  {x.MF_NAME} ({x.MF}){" "}
                </div>
              </div>
            ))
        : null}
    </Fragment>
  )
}
export default MfrSelection
