import React, { Fragment } from "react"

const SharePoint = props => {
  const { inspectionID, currentSU, currentMF, getSharePointFiles, setShowSharePointFiles, showSharePointFiles, setSharePointFiles, sharePointPath, createsharePointPDF, editMode } = props

  const handleClick_add = () => {
    const url = "https://macysinc.sharepoint.com/sites/OSO/".concat(sharePointPath, "/Forms/AllItems.aspx?id=%2Fsites%2FOSO%2F", sharePointPath, "%2F", "SU", currentSU, "MF", currentMF, "&dt=", new Date().getTime())
    var newWindow = window.open(url, "_blank", "noopener,noreferrer")
    if (newWindow) newWindow.opener = null
  }

  const handleClick_view = () => {
    const url = "https://macysinc.sharepoint.com/sites/OSO/".concat(sharePointPath, "/Forms/AllItems.aspx?id=%2Fsites%2FOSO%2F", sharePointPath, "%2F", "SU", currentSU, "MF", currentMF, "&view=7&q=", inspectionID.mc, "&FilterField1=Inspection_x0020_ID&FilterValue1=", inspectionID.mc, "-", inspectionID.iteration, "-", inspectionID.type[0], "&dt=", new Date().getTime())
    var newWindow = window.open(url, "_blank", "noopener,noreferrer")
    if (newWindow) newWindow.opener = null
  }

  const handleToggleSharePointFiles = () => {
    if (showSharePointFiles === true) {
      setShowSharePointFiles(false)
      setSharePointFiles([])
    } else {
      setShowSharePointFiles(true)
      getSharePointFiles()
    }
  }

  const handleGetSharePointFiles = () => {
    if (showSharePointFiles) {
      setSharePointFiles([])
      getSharePointFiles()
    }
  }

  return (
    <Fragment>
      <div className="sharePoint_section">Document Management on SharePoint</div>
      <div className="sharePoint_grid">
        <div className="sharePoint_cell">
          <img src="../static/img/sharePointx.jpg" alt="sharePoint" className="sharePoint_photo" />
        </div>
        <div></div>

        <div className="sharePoint_cell">
          View files&nbsp;
          <i
            className="fa  fa-sign-in clickable"
            onClick={() => {
              handleClick_view()
            }}
          ></i>
        </div>

        {editMode ? (
          <div className="sharePoint_cell">
            Add files&nbsp;
            <i
              className="fa  fa-sign-in clickable"
              onClick={() => {
                handleClick_add()
              }}
            ></i>
          </div>
        ) : (
          <div className="sharePoint_cell"></div>
        )}

        {/* Combine photo into PDF */}
        {/* disable below 3/9/22 */}
        {false ? (
          <div className="sharePoint_cell">
            Create Photo Album&nbsp;
            <i
              className="fa  fa-file-pdf-o clickable"
              onClick={() => {
                createsharePointPDF()
              }}
            ></i>
          </div>
        ) : null}

        <div className="sharePoint_cell">
          Instant View : &nbsp;&nbsp;
          {showSharePointFiles ? "Collapse" : "Expand"}
          &nbsp;
          <i
            className={"clickable fa " + (showSharePointFiles ? "fa-minus" : "fa-plus")}
            onClick={() => {
              handleToggleSharePointFiles()
            }}
          ></i>
          &nbsp; &nbsp;
          <i
            id="sharePoint_refresh"
            className="clickable fa fa-refresh"
            onClick={() => {
              handleGetSharePointFiles()
            }}
          ></i>
          &nbsp; &nbsp;
        </div>
      </div>
    </Fragment>
  )
}
export default SharePoint
