import { React, useEffect, useState } from "react"

const ItemTotal = props => {
  const { items, mcTable, addMC, currentSU, currentMF, setItemSearchString, editMode, setDisplayAlert, ship, setShip, poList, totalShip, totalInspect, totalSample, reset, handleDeleteShip, handleDeletePO, setPOList, aqlTable } = props

  const [mc, setMC] = useState("")

  useEffect(() => {
    const listener = event => {
      if (event.code === "Enter" || event.code === "NumpadEnter") {
        //console.log("Enter key was pressed. Run your function.");
        event.preventDefault()
        document.getElementById("itemTable_MCBtn").click()
      }
    }

    if (document.getElementById("itemTable_MC")) document.getElementById("itemTable_MC").addEventListener("keydown", listener)

    return () => {
      if (document.getElementById("itemTable_MC")) document.getElementById("itemTable_MC").removeEventListener("keydown", listener)
    }
  }, [])

  /* Item Total */

  useEffect(() => {
    const listener = event => {
      if (event.code === "Enter" || event.code === "NumpadEnter") {
        //console.log("Enter key was pressed. Run your function.");
        event.preventDefault()
        document.getElementById("highLightItemBtn").click()
      }
    }
    var el = document.getElementById("itemTotal_searchString")

    if (el) {
      el.addEventListener("keydown", listener)
      return () => {
        el.removeEventListener("keydown", listener)
      }
    }
  }, [])

  const sortTable = (sort, order) => {
    const nShip = [...ship]
    /*table = document.getElementById("itemTable") */
    if (sort === 0 && order === "asc") {
      nShip.sort(function (a, b) {
        return a.po_no > b.po_no ? 1 : -1
      })
    } else if (sort === 0 && order === "desc") {
      nShip.sort(function (a, b) {
        return a.po_no < b.po_no ? 1 : -1
      })
    } else if (sort === 1 && order === "asc") {
      nShip.sort(function (a, b) {
        return a.item_no > b.item_no ? 1 : -1
      })
    } else if (sort === 1 && order === "desc") {
      nShip.sort(function (a, b) {
        return a.item_no < b.item_no ? 1 : -1
      })
    }
    setShip(nShip)
    setItemSearchString("")
  }

  //* add new MC

  const handleInputChange = event => {
    const value = event.target.value
    setMC(value)
  }

  const validateMC = () => {
    //event.preventDefault()
    const inMCTable = mcTable.find(x => x.mc_no === mc)
    const inItems = items.find(x => x.mc_no === mc)
    const duplicate = ship.find(x => x.mc_no === mc)
    const validMF = mcTable.find(x => x.mc_no === mc && x.su_no === currentSU && x.mf_no === currentMF)

    if (!inMCTable) {
      setDisplayAlert({ message: "This MC is not found in forthcoming or may not belong to this manufacturer!", position: 1, timeout: 4000, color: "alert__red" })
    } else if (!validMF) {
      displayAlert("This MC seems not for this manufacturer!")
    } else if (duplicate) {
      displayAlert("MC was already entered in the ship item list, please check ! ")
    } else if (inMCTable || !inItems) {
      addMC(mc)
    }
  }

  //encapsulate the props function as only message is different in this page
  const displayAlert = message => {
    setDisplayAlert({ message: message, position: 1, timeout: 1500, color: "alert__red" })
  }

  const handleShipQty = (po_no, item_no) => event => {
    let value = event.target.value

    if (value === "") {
      return
    }
    if (parseInt(value) > 20000) {
      value = 20000
      setDisplayAlert({ message: "Number of ship units is too large!", position: 1, timeout: 3000, color: "alert__red" })
    }

    let nShip = [...ship]
    const index = nShip.findIndex(r => r.po_no === po_no && r.item_no === item_no)
    const newEntry = nShip.find(r => r.po_no === po_no && r.item_no === item_no)
    nShip[index] = { ...newEntry, ship_qty: parseInt(value) }
    //console.log("handleShipQty", nShip)

    // recalculate sample
    const total_qty = nShip.reduce((prev, curr) => prev + parseInt(curr.ship_qty), 0) // total new ship qty
    const aql = aqlTable.filter(x => x.size_start <= total_qty && x.size_end >= total_qty)
    nShip = nShip.map(x => ({ ...x, sample_qty: Math.round((x.ship_qty / total_qty) * aql[0]["25sample"]), inspect_qty: Math.round((x.ship_qty / total_qty) * aql[0]["25sample"]) }))

    setShip(nShip)

    const tShip = nShip.filter(x => x.po_no === po_no).reduce((prev, curr) => prev + parseInt(curr.ship_qty), 0) // total new ship qty
    const nPO = [...poList]
    //console.log("nPO", nPO)
    const indexPO = nPO.findIndex(r => r.po_no === po_no)
    const newEntryPO = nPO.find(r => r.po_no === po_no)
    nPO[indexPO] = { ...newEntryPO, ship_qty: tShip }
    setPOList(nPO)
  }

  const handleInspQty = (po_no, item_no) => event => {
    let value = event.target.value
    const nShip = [...ship]
    const index = nShip.findIndex(r => r.po_no === po_no && r.item_no === item_no)
    const newEntry = nShip.find(r => r.po_no === po_no && r.item_no === item_no)
    nShip[index] = { ...newEntry, inspect_qty: parseInt(value) }
    setShip(nShip)
  }

  return (
    // present the table

    <div className="itemTable_2col">
      <div>
        <div className="itemTable_Section">
          <div>Add MC:</div>
          &nbsp;
          <div>{editMode ? <input type="text" id="itemTable_MC" name="itemTable_mc" justify="left" placeholder="Additional MC here" onChange={handleInputChange} /> : <input type="text" id="itemTable_MC" name="itemTable_MC" justify="left" placeholder="Include additional MC here" readOnly onChange={handleInputChange} />}</div>
          &nbsp;&nbsp;
          <div>
            {editMode ? (
              <i
                id="itemTable_MCBtn"
                className="fa fa-plus clickable"
                onClick={() => {
                  validateMC(mc)
                }}
              ></i>
            ) : null}
          </div>
        </div>
        <div className="itemTable_Header">
          <div>PO</div>
          <div>DIV</div>
          <div>Order Qty</div>
          <div>Ship Qty</div>
          <div>%Ship</div>
          <div>Delete</div>
        </div>

        <div id="itemTableScroller" className="itemTable_GridContainer">
          {!poList
            ? null
            : poList.map((x, index) => (
                <div key={index} className="itemTable_Detail">
                  <div>{x.po_no}</div>
                  <div>{x.customer}</div>
                  <div>{x.order_qty}</div>
                  <div>{x.ship_qty}</div>
                  <div>{Math.round((x.ship_qty / x.order_qty) * 100)}%</div>
                  <div>
                    {/* <div>{editMode ? <button onClick={e => deletePO(x.po_no)}>x</button> : null}</div> */}
                    {editMode ? (
                      <i
                        className="fa fa-times clickable"
                        onClick={() => {
                          handleDeletePO(x.mc_no, x.po_no)
                        }}
                      ></i>
                    ) : null}
                  </div>
                </div>
              ))}
        </div>
      </div>
      <div></div>
      <div>
        <div className="itemTotal_Section">
          <div>Highlight Item :</div>

          <div>
            <input type="text" name="itemTotal_searchString" id="itemTotal_searchString" justify="left" onInput={e => (e.target.value = ("" + e.target.value).toUpperCase())} placeholder="Enter item here" />
          </div>

          <div>
            <i
              className="fa fa-search clickable"
              id="highLightItemBtn"
              onClick={() => {
                setItemSearchString(document.getElementById("itemTotal_searchString").value)
              }}
            ></i>
          </div>

          <div>
            Reset Inspect &nbsp;
            <i
              className="fa fa-refresh itemTotal_refresh_color clickable"
              onClick={() => {
                reset()
              }}
            ></i>
          </div>

          {/* <div>
            Undo
            <i
              className="fa fa-undo itemTotal_undo_color clickable "
              onClick={() => {
                getLocal()
              }}
            ></i>
          </div> */}
        </div>

        <div className="itemTotal_Header">
          <div>
            PO
            <i
              className="fa fa-chevron-circle-up clickable"
              onClick={() => {
                sortTable(0, "asc")
              }}
            ></i>
            &nbsp;
            <i
              className="fa fa-chevron-circle-down clickable"
              onClick={() => {
                sortTable(0, "desc")
              }}
            ></i>
          </div>
          <div>
            By Item
            <i
              className="fa fa-chevron-circle-up clickable"
              onClick={() => {
                sortTable(1, "asc")
              }}
            ></i>
            &nbsp;
            <i
              className="fa fa-chevron-circle-down clickable"
              onClick={() => {
                sortTable(1, "desc")
              }}
            ></i>
          </div>
          <div>Ship</div>
          <div>Sample</div>
          <div>Inspect</div>
          <div>Delete</div>
        </div>

        <div id="scroller" className="itemTotal_GridContainer">
          {!ship
            ? null
            : ship.map((x, index) => (
                <div key={index} className="itemTotal_Detail">
                  <div>{x.po_no}</div>
                  <div>{x.item_no}</div>
                  <div>
                    <input type="number" id="itemTotal_shipQty" name="itemTotal_shipQty" value={x.ship_qty || 1} min="1" max="15000" justify="left" onChange={handleShipQty(x.po_no, x.item_no)} readOnly={editMode ? "" : "readOnly"} />
                  </div>
                  <div>{x.sample_qty}</div>

                  <div>
                    <input type="number" id="itemTotal_InspectQty" name="itemTotal_InspectQty" value={x.inspect_qty || 1} min="1" max="15000" justify="left" onChange={handleInspQty(x.po_no, x.item_no)} readOnly={editMode ? "" : "readOnly"} />
                  </div>

                  <div>
                    {editMode ? (
                      <i
                        className="fa fa-times clickable"
                        onClick={() => {
                          handleDeleteShip(x.mc_no, x.po_no, x.item_no)
                        }}
                      ></i>
                    ) : null}
                  </div>
                </div>
              ))}
        </div>

        <div className="itemTotal_Total">
          <div> Total :</div>
          <div></div>
          <div>{totalShip}</div>
          <div>{totalSample}</div>
          <div className={totalSample !== totalInspect ? "highlightSection" : null}>{totalInspect}</div>
          <div></div>
        </div>
      </div>
    </div>
  )
}

export default ItemTotal
