import React, { Fragment } from "react"
//import axios from "axios"
import { Spinner } from "./Spinner"

const SearchKey = props => {
  const { checkDuplicateID, mcTable, setDisplayAlert, inspectionID, setInspectionID, clearScreen, searchInspByMC, editMode, inspType, showFull } = props

  const handleKeyChange = event => {
    const id = event.target.id
    const { value } = event.target

    switch (id) {
      case "searchKey_mc":
        setInspectionID({ ...inspectionID, mc: value })
        if (value.length >= 3) searchInspByMC(value)
        // pass first few characters of MC entered
        else clearScreen()
        break
      case "searchKey_iteration":
        setInspectionID({ ...inspectionID, iteration: value })
        break
      case "searchKey_type":
        setInspectionID({ ...inspectionID, type: value })
        break
      default:
        break
    }
  }

  const validateMC = id => {
    const found = mcTable.find(x => x.mc_no === id.mc)
    clearScreen()

    if (!found) {
      displayAlert("This MC doesn't exists or it doesn't belong to this manufacturer!")
    } else {
      checkDuplicateID(id)
    }
  }

  function isInvalidID() {
    if (inspectionID.mc === "" || inspectionID.iteration === "Select" || inspectionID.iteration === "" || inspectionID.type === "Select") return true
    else return false
  }

  //encapsulate the props function as only message is different in this page
  const displayAlert = message => {
    setDisplayAlert({ message: message, position: 0, timeout: 3000, color: "alert__red" })
  }

  return (
    <Fragment>
      <div className="searchKey">
        <div className="searchKey_pair">
          <label>MC# : </label>
          <input type="text" name="searchKey_mc" id="searchKey_mc" onChange={handleKeyChange} readOnly={showFull ? "readOnly" : ""} />
        </div>
        <div className="searchKey_pair">
          <label>Iteration : </label>
          <input type="number" name="searchKey_iteration" id="searchKey_iteration" placeholder="Iteration" min="1" max="9" onChange={handleKeyChange} readOnly={showFull ? "readOnly" : ""} />
        </div>
        <div className="searchKey_pair">
          <label>Type : </label>
          <select name="searchKey_type" id="searchKey_type" onChange={handleKeyChange}>
            <option value="Select">Select</option>
            {inspType &&
              inspType.map(x => (
                <option key={x.insp_type} value={x.insp_type} disabled={showFull ? "disabled" : ""}>
                  {x.insp_type_long}
                </option>
              ))}
          </select>
        </div>
        <div></div>
        {!editMode ? (
          <div>
            {!isInvalidID() ? (
              <div>
                <button
                  onClick={() => {
                    validateMC(inspectionID)
                  }}
                >
                  Create Inspection
                </button>
              </div>
            ) : null}
          </div>
        ) : null}
      </div>
      <Spinner area="searchKey" />
    </Fragment>
  )
}

export default SearchKey
