import React, { useState, Fragment, useEffect, useCallback } from "react"

const DefectOptions = props => {
  // remove columns : mc_no and id in prepration for calculating total by item#
  const { editMode, defects, setDisplayAlert, setDefectLanguage, defectLanguage, defectList, showFull, addToDefectList } = props

  const [currentProduct, setCurrentProduct] = useState("Select")

  const [currentDefectArea, setCurrentDefectArea] = useState("")
  const [currentDefectAreaCode, setCurrentDefectAreaCode] = useState("")

  const [currentDefectDetail, setCurrentDefectDetail] = useState("")
  const [currentDefectDetailCode, setCurrentDefectDetailCode] = useState("")

  const [currentMajor, setCurrentMajor] = useState("")
  const [currentMinor, setCurrentMinor] = useState("")

  const [optionListDefect, setOptionListDefect] = useState([])
  const [optionListDefectDetail, setOptionListDefectDetail] = useState([])

  //  prepare for the defect selection - need to associate based on each specific product and then its associted defect.
  //  Trying to shortlist the defect code based on product

  const buildOptDefectList = useCallback(
    (choice, value) => {
      //console.log("Callback called")
      let _list = ""
      switch (choice) {
        case "defect": // select defect
          _list = defectList.filter(x => x.product === value && x.lang === defectLanguage)
          _list = _list.map(({ defect_area, defect_code }) => ({ code_area: "".concat(defect_code, "~", defect_area) }))
          _list = _list.map(({ code_area }) => code_area)
          _list = [...new Set(_list)].sort()
          setOptionListDefect(_list)
          break
        case "detail": // select detail
          const _defect_code = value.split("~")[0] // based defect code
          _list = defectList.filter(x => x.product === currentProduct && x.lang === defectLanguage && x.defect_code === _defect_code)
          _list = _list.map(({ defect_detail_code, defect_detail }) => ({ code_detail: "".concat(defect_detail_code, "~", defect_detail) }))
          _list = _list.map(({ code_detail }) => code_detail)
          _list = [...new Set(_list)].sort()
          setOptionListDefectDetail(_list)
          break
        default:
          break
      }
    },
    [defectList, currentProduct, defectLanguage]
  )

  useEffect(() => {
    if (currentProduct && currentProduct !== "Select") {
      buildOptDefectList("defect", currentProduct)
    }
  }, [currentProduct, buildOptDefectList])

  useEffect(() => {
    if (!showFull) {
      setCurrentDefectArea("")
      setCurrentDefectAreaCode("")
      setCurrentMajor("")
      setCurrentMinor("")
    } else if (showFull && defects.length > 0) {
      setCurrentProduct(defects[0].product)
      //buildOptDefectList("defect", defects[0].product)
    } else if (showFull && defects.length === 0) {
      setCurrentProduct("")
      setCurrentDefectArea("Select")
      setCurrentMajor("")
      setCurrentMinor("")
      setCurrentDefectAreaCode("")
      setCurrentDefectDetailCode("")
    }
  }, [showFull, defects])

  const handleSelectChangeProduct = event => {
    const { value } = event.target

    // check if the switching language (=value) exists
    const total_lang_rec = defectList.filter(x => x.product === value && x.lang === defectLanguage)
    // Following if block must precede other

    if (total_lang_rec.length === 0) {
      setDisplayAlert({ message: "No product selected or Language has NOT been set for this product !!", position: 2, timeout: 2500, color: "alert__red" })
      return
    }

    if (disableProductSelect() === "disabled") {
      if (value !== defects[0].product) {
        setDisplayAlert({ message: "Product '" + defects[0].product + "' has already been chosen !! Please empty all defects if you decide to change product ! ", position: 2, timeout: 2500, color: "alert__red" })
        return
      }
      document.getElementById("defectOptionsProduct").value = defects[0].product
      document.getElementById("defectOptionsDefectArea").value = "Select"
      setCurrentProduct(defects[0].product)
    } else {
      setCurrentProduct(value)
    }

    buildOptDefectList("defect", value)
    setCurrentDefectArea("Select")
    setCurrentMajor("")
    setCurrentMinor("")
    setCurrentDefectAreaCode("")
    setCurrentDefectDetailCode("")
  }

  function disableProductSelect() {
    if (defects.length > 0) {
      return "disabled"
    } else return null
  }

  const handleSelectChangeDefect = event => {
    const { value } = event.target
    const area_array = value.split("~")
    setCurrentDefectAreaCode(area_array[0])
    setCurrentDefectArea(area_array[1])
    buildOptDefectList("detail", value)
    setCurrentDefectDetail("Select")
    setCurrentMajor("")
    setCurrentMinor("")
  }

  const handleSelectChangeDefectDetail = event => {
    const { value } = event.target
    const detail_array = value.split("~")
    setCurrentDefectDetailCode(detail_array[0])
    setCurrentDefectDetail(detail_array[1])
    setCurrentMajor("")
    setCurrentMinor("")
  }

  const handleCurrentMajorChange = event => {
    const { value } = event.target
    setCurrentMajor(value)
  }

  const handleCurrentMinorChange = event => {
    const { value } = event.target
    setCurrentMinor(value)
  }

  const handleChangeDefectLanguage = event => {
    const { value } = event.target

    // check if the switching language (=value) exists
    const total_lang_rec = defectList.filter(x => x.product === currentProduct && x.lang === value)
    // Following if block must precede other

    if (total_lang_rec.length === 0) {
      setDisplayAlert({ message: "No product selected or Language has NOT been set for this product !!", position: 2, timeout: 2500, color: "alert__red" })
      return
    }
    setDefectLanguage(value)
    setCurrentDefectArea("Select")
    setOptionListDefectDetail([])
  }

  const handleAddDefect = event => {
    //event.preventDefault()
    const major_int = isNaN(parseInt(currentMajor)) // check if empty, return true if empty
    const minor_int = isNaN(parseInt(currentMinor)) // check if empty, return true if empty

    if (currentProduct === "Select" || currentDefectArea === "Select" || currentDefectDetail === "Select") {
      props.setDisplayAlert({ message: "Please make sure you select Product, Defect and Defect Detail ! ", position: 2, timeout: 2500, color: "alert__red" })
    } else if (major_int || minor_int || (currentMajor < 1 && currentMinor < 1)) {
      props.setDisplayAlert({ message: "Please make sure you enter either Major and Minor quantity ! ", position: 2, timeout: 2500, color: "alert__red" })
    } else {
      addToDefectList(currentProduct, currentDefectArea, currentDefectDetail, currentMajor, currentMinor, currentDefectAreaCode, currentDefectDetailCode, defectLanguage)
    }
  }

  return (
    // present the table

    <Fragment>
      <div className="defectOption_header">
        <div>Language</div>

        <div>&nbsp;&nbsp;&nbsp;&nbsp;</div>
        <div>
          <select className="defectOption_language  clickable" onChange={handleChangeDefectLanguage} value={defectLanguage}>
            <option value="en">English</option>
            <option value="zh">Chinese</option>
          </select>
        </div>
      </div>

      <div className="defectOption">
        <div className="defectOption_product">
          <select onChange={handleSelectChangeProduct} id="defectOptionsProduct" className="clickable" value={currentProduct}>
            {/* <option disabled={disableProductSelect()}  value="Select">Select</option> */}
            <option value="Select">Select</option>
            <option value="Fashion Jewelry" disabled={editMode ? "" : "disabled"}>
              Fashion Jewelry
            </option>
            <option value="Footwear" disabled={editMode ? "" : "disabled"}>
              Footware
            </option>
            <option value="Handbags" disabled={editMode ? "" : "disabled"}>
              Handbags
            </option>
            <option value="Hardlines" disabled={editMode ? "" : "disabled"}>
              Hardlines
            </option>
            <option value="Softlines" disabled={editMode ? "" : "disabled"}>
              Softlines
            </option>
          </select>
        </div>

        <div className="defectOption_defectArea">
          <select onChange={handleSelectChangeDefect} id="defectOptionsDefectArea" className="clickable" value={currentDefectAreaCode + "~" + currentDefectArea}>
            <option value="Select">Select</option>
            {optionListDefect.map((x, index) => (
              <option key={index} value={x} disabled={editMode ? "" : "disabled"}>
                {x}
              </option>
            ))}
          </select>
        </div>

        <div className="defectOption_defectDetail">
          <select onChange={handleSelectChangeDefectDetail} id="defectOptionsDefectDetail" className="clickable" value={currentDefectDetailCode + "~" + currentDefectDetail}>
            <option value="Select">Select</option>
            {optionListDefectDetail.map((x, index) => (
              <option key={index} value={x} disabled={editMode ? "" : "disabled"}>
                {x}
              </option>
            ))}
          </select>
        </div>

        <div className="defectOption_major">
          <input type="number" name="major" placeholder=" Major" value={currentMajor} min="0" max="5" onChange={handleCurrentMajorChange} readOnly={editMode ? "" : "readOnly"} />
        </div>

        <div className="defectOption_minor">
          <input type="number" name="minor" placeholder=" Minor" value={currentMinor} min="0" max="5" onChange={handleCurrentMinorChange} readOnly={editMode ? "" : "readOnly"} />
        </div>

        <div>
          {editMode ? (
            <button
              onClick={() => {
                handleAddDefect()
              }}
            >
              Add
            </button>
          ) : null}
        </div>
        <div></div>
      </div>
    </Fragment>
  )
}

export default DefectOptions
