import React, { Fragment } from "react"

const DefectList = props => {
  const { defects, editMode, isCriticalDefect, defectLanguage, defectList } = props

  function defect_lang(product, area_code, detail) {
    //console.log("input", product, area_code, detail)
    const defect = defectList.filter(x => x.product === product && x.defect_code === area_code && x.defect_detail_code === detail && x.lang === defectLanguage)
    return defect[0]
  }

  return (
    // present the table
    // adding span below appends ... to the off-limit text
    <Fragment>
      <div className="defectList">
        <div className="defectList_gridHeader">
          <div>Product</div>
          <div>Defect</div>
          <div>Defect Detail</div>
          <div className="defectList_major_header">
            Major/<br></br>Critical
          </div>
          <div className="defectList_minor_header">Minor</div>
          <div>Delete</div>
        </div>

        {defects.length > 0 ? (
          defects.map(x => (
            <div className="defectList_gridDetail" key={x.id}>
              <div className="defectList_gridItem defectList_product">{x.product}</div>
              <div className="defectList_gridItem defectList_defectArea">{x.defect_area_code + "~" + defect_lang(x.product, x.defect_area_code, x.defect_detail_area).defect_area}</div>
              <div className="defectList_gridItem  defectList_defectDetail" style={{ color: isCriticalDefect(x.product, x.defect_detail_area) ? "red" : "inherit" }}>
                <span>{x.defect_detail_area + "~" + defect_lang(x.product, x.defect_area_code, x.defect_detail_area).defect_detail}</span>
              </div>
              <div className="defectList_major">{x.major}</div>
              <div className="defectList_minor">{x.minor}</div>
              <div className="defectList_deleteButton">
                {editMode ? (
                  <button
                    onClick={() => {
                      props.deleteDefect(x.id)
                    }}
                  >
                    &times;
                  </button>
                ) : null}
              </div>
              <div></div>
            </div>
          ))
        ) : (
          <div>
            <div colSpan={3}>No Defects</div>
          </div>
        )}
      </div>
    </Fragment>
  )
}

export default DefectList
