import React, { Fragment } from "react"
import { Spinner } from "./Spinner"

const SearchedMC = props => {
  const { setInspectionID, handleSearch, searchedMC, setShowSearchedMC, currentMF } = props

  const newSearchedMC = []
  for (var i = 0; i < searchedMC.length; i++) {
    if (searchedMC[i].main["mf_no"] === currentMF) {
      newSearchedMC.push(searchedMC[i]._id)
    }
  }

  const handleClick = id => {
    setInspectionID(id)
    handleSearch(id)
    // Set search fields in SearchKey.js
    document.getElementById("searchKey_mc").value = id.mc
    document.getElementById("searchKey_iteration").value = id.iteration
    document.getElementById("searchKey_type").value = id.type
    setShowSearchedMC(false)
  }

  return (
    // present the table
    <Fragment>
      <div className="searchedMC">
        <div className="searchedMC_header1 searchedMC_header">
          <div></div>
          <div>MC</div>
          <div className="searchedMC_detail_center">Iteration</div>
          <div className="searchedMC_detail_center">Type</div>
        </div>
        <div className="searchedMC_header2 searchedMC_header">
          <div></div>
          <div>MC</div>
          <div className="searchedMC_detail_center">Iteration</div>
          <div className="searchedMC_detail_center">Type</div>
        </div>
        <div className="searchedMC_header3 searchedMC_header">
          <div></div>
          <div>MC</div>
          <div className="searchedMC_detail_center">Iteration</div>
          <div className="searchedMC_detail_center">Type</div>
        </div>

        {newSearchedMC.length > 0
          ? newSearchedMC.map((x, index) => (
              <div
                className="searchedMC_detail"
                key={index}
                onClick={() => {
                  handleClick({ mc: x.mc, iteration: x.iteration, type: x.type })
                }}
              >
                <div></div>
                <div>{x.mc}</div>
                <div className="searchedMC_detail_center">{x.iteration}</div>
                <div className="searchedMC_detail_center">{x.type}</div>
              </div>
            ))
          : null}
      </div>
      <Spinner area="mfrSelection" />
    </Fragment>
  )
}

export default SearchedMC
