import React, { Fragment } from "react"

const Result = props => {
  const { setMisc, misc, editMode, visualResult, calculateFinalResult } = props

  function calculate_final_result(misc) {
    if (misc.packing_result === "Failed" || (misc.measurement_result === "Failed") | (visualResult === "Failed")) {
      return "Failed"
    } else {
      return "Passed"
    }
  }

  const handleChange = (element, param2) => event => {
    let value = event.target.value
    if (misc) {
      let newMisc = { ...misc }
      switch (element) {
        case 1: //a  Packing
          newMisc = { ...newMisc, packing_result: value }
          if (value === "Failed") {
            newMisc = { ...newMisc, final_result: "Failed" }
          } else {
            newMisc = { ...newMisc, final_result: calculate_final_result(newMisc) }
          }
          break
        case 2: //b  Measurement
          newMisc = { ...newMisc, measurement_result: value }
          if (value === "Failed") {
            newMisc = { ...newMisc, final_result: "Failed" }
          } else {
            newMisc = { ...newMisc, final_result: calculate_final_result(newMisc) }
          }
          break
        default:
          console.log("No element")
      }
      setMisc(newMisc)
    }
  }

  return (
    <Fragment>
      <div className="result">
        <div className="result_f result_grid_f"> Inspection Result Summary</div>
        <div></div>
        <div></div>

        <div className="result_a result_grid_a">
          <div className="result_title">Packing</div>
          <select id="result_packingResult" name="misc_packingResult" value={misc.packing_result || "Select"} justify="left" placeholder="Packing Result" onChange={handleChange(1)}>
            <option value="Select" disabled="disabled">
              Select
            </option>
            <option value="Passed" disabled={editMode ? "" : "disabled"}>
              Passed
            </option>
            <option value="Failed" disabled={editMode ? "" : "disabled"}>
              Failed
            </option>
          </select>
        </div>

        <div className="result_b result_grid_b">
          <div className="result_title">Measurement</div>
          <select id="result_measurementResult" name="misc_measurementResult" value={misc.measurement_result || "Select"} justify="left" placeholder="Measurement result" onChange={handleChange(2)}>
            <option value="Select" disabled="disabled">
              Select
            </option>
            <option value="Passed" disabled={editMode ? "" : "disabled"}>
              Passed
            </option>
            <option value="Failed" disabled={editMode ? "" : "disabled"}>
              Failed
            </option>
          </select>
        </div>

        <div className="result_c result_grid_c">
          <div className="result_title">Critical / Major / Minor</div>
          <input type="text" id="result_defectiveResult" name="result_defectiveResult" value={visualResult.defective_result || ""} justify="left" placeholder="" readOnly="readOnly" />
        </div>

        <div className="result_d result_grid_d">
          <div className="result_title">Sample Threshold (Major / Minor)</div>
          <input type="text" id="result_sampleThreshold" name="result_sampleThreshold" value={visualResult.sample_threshold || ""} justify="left" placeholder="" readOnly="readOnly" />
        </div>

        <div className="result_e result_grid_e">
          <div className="result_title">Final Result</div>
          <input type="text" id="result_finalResult" name="result_finalResult" value={calculateFinalResult()} justify="left" placeholder="" readOnly="readOnly" />
        </div>

        <div className="result_g result_grid_g">
          <div className="result_title">Visual Result</div>
          <input type="text" id="result_visualResult" name="result_visualResult" value={visualResult.visual_result || "Passed"} justify="left" placeholder="" readOnly="readOnly" />
        </div>
      </div>
    </Fragment>
  )
}
export default Result
