import React, { Fragment } from "react"
import { Spinner } from "./Spinner"

const ShowSpinner = props => {
  const { position } = props

  return (
    <Fragment>
      {position === 1 ? <Spinner area="getUserProfile" spinnerMessage="Loading your profile, please wait... " /> : null}
      {position === 2 ? (
        <div>
          <Spinner area="printReport" spinnerMessage="Preparing the report, please wait... " />
          <Spinner area="saveInspection" spinnerMessage="Saving the changes, please wait... " />
          <Spinner area="deleteInspection" spinnerMessage="Deleting, please wait... " />
          <Spinner area="createInspection" spinnerMessage="Loading, please wait... " />
          <Spinner area="getMCtable" spinnerMessage="Getting MC data, please wait... " />
        </div>
      ) : null}
      {position === 3 ? (
        <div>
          <Spinner area="sharePointFiles" spinnerMessage="Loading SharePoint info, please wait... " />
          <Spinner area="downloadSharePointfiles" spinnerMessage="Downloading SharePoint info, please wait... " />
          <Spinner area="sharePointUpload" spinnerMessage="Uploading file(s) to SharePoint, please wait... " />
        </div>
      ) : null}
      {position === 10 ? <Spinner area="pastInspection" spinnerMessage="Loading past inspections, please wait... " /> : null}
    </Fragment>
  )
}
export default ShowSpinner
