import React, { Fragment } from "react"

const ShowSharePointFiles = props => {
  const { sharePointFiles, downloadSharePointFile, showSharePointFiles, convertToLocal, editMode, handleDeleteSPfile } = props

  const ext = [".jpg", ".png"]
  function isSupportPicExt(filename) {
    for (let i = 0; i < ext.length; i++) {
      if (filename.toLowerCase().includes(ext[i])) {
        return true
      }
    }
    return false
  }

  // const openPhoto = url => {
  //   const newWindow = window.open(url, "_blank", "noopener,noreferrer")
  //   if (newWindow) newWindow.opener = null
  // }

  return (
    <Fragment>
      {showSharePointFiles ? (
        <div className="showSharePointFiles_section">
          <div className="showSharePointFiles_grid_header">
            <div>File Name</div>
            <div>Last Editor</div>
            <div>Last Edit Time</div>
            <div>File Size</div>
            <div>Download</div>
            <div>File Type</div>
            <div>Delete</div>
          </div>

          <div>
            {sharePointFiles.length > 0
              ? sharePointFiles.map(line =>
                  line.filename === "No files" ? (
                    <div className="sharePointFiles_no_files">{line.filename}</div>
                  ) : (
                    <div key={line.unique_id} className="showSharePointFiles_grid_detail">
                      <div>{line.filename}</div>
                      <div>{line.editor === "SharePoint App" ? line.last_editor : line.editor}</div>
                      <div>{convertToLocal(line.modified)}</div>
                      <div>{line.size}</div>
                      <div>
                        <i
                          className="clickable fa fa-download"
                          onClick={() => {
                            downloadSharePointFile(line.filename)
                          }}
                        ></i>
                      </div>
                      {isSupportPicExt(line.filename) ? (
                        <div className="sharePointFiles_photoBlock">
                          <img src={"/showPhoto?folder=" + line.folder + "&filename=" + line.filename} alt="" />
                        </div>
                      ) : (
                        <div>
                          <i className="fa fa-file fa-file-doc"></i>
                        </div>
                      )}

                      {editMode ? (
                        <div>
                          <button
                            id="sharePointFiles_deleteButton"
                            onClick={() => {
                              handleDeleteSPfile(line.unique_id)
                              //props.deleteDefect(x.id)
                            }}
                          >
                            &times;
                          </button>
                        </div>
                      ) : (
                        <div>&nbsp;</div>
                      )}
                    </div>
                  )
                )
              : null}
          </div>
        </div>
      ) : null}
    </Fragment>
  )
}
export default ShowSharePointFiles
