import { Route, Routes, HashRouter as Router, Link } from "react-router-dom"
import React, { useState, useEffect } from "react"
import axios from "axios"
import AppInspection from "./AppInspection"
import Home from "./Home"
import XDaysResult from "./XDaysResult"
import UserProfile from "./components/UserProfile"
import { trackPromise } from "react-promise-tracker"

const logout = () => {
  localStorage.clear()
  window.location = "/logout"
}

const gotoPowerBI = () => {
  const url = "https://app.powerbi.com/reportEmbed?reportId=f0a7cd56-e22b-490a-9ad7-012d4a0d5251&autoAuth=true&filterPanelEnabled=False&pageName=ReportSectionf1be12cf083e1c0eba15"

  const newWindow = window.open(url, "_blank", "noopener,noreferrer")
  if (newWindow) newWindow.opener = null
}

const App = () => {
  const [userProfile, setUserProfile] = useState({})

  useEffect(() => {
    trackPromise(
      axios({
        url: "/api/getUserProfile",
        method: "POST",
        headers: {
          "Content-type": "application/json",
          api: "yes"
        }
        //data: JSON.stringify({})   seems like this line has to be removed to avoid error from Heroku as this line is passing empty parameter.
      })
        .then(response => response.data)
        .then(response => {
          setUserProfile(response["userProfile"])
        })
        .catch(error => {
          console.log(error)
        })
    )
  }, [])

  return (
    <div className="container app_container">
      <div className="flex-row">
        <div className="flex-large">
          <UserProfile userProfile={userProfile} />
          <Router>
            <nav>
              <ul className="app_header">
                <li>
                  <Link to="/" className="link">
                    Home
                  </Link>
                </li>
                <li>
                  <Link to="/appInspection" className="link">
                    Inspection
                  </Link>
                </li>

                {userProfile.email !== undefined && userProfile.email.includes("macys.com") ? (
                  <li>
                    <Link to="/xDaysResult" className="link">
                      Past Inspection Results
                    </Link>
                  </li>
                ) : null}

                {userProfile.email !== undefined && userProfile.email.includes("macys.com") ? (
                  <li
                    className="link"
                    onClick={() => {
                      gotoPowerBI()
                    }}
                  >
                    Power BI
                  </li>
                ) : null}

                <button
                  className="fa fa-sign-out"
                  onClick={() => {
                    logout()
                  }}
                >
                  Logout
                </button>
                {/* <div className="userProfile_photo">{userProfile.email ? userProfile.email.includes("@macys.com") ? <img src={"/api/getPhoto"} aria-hidden alt="Searching for photo..." width="60" height="60" /> : <img src="static/img/anonymous.jpg" aria-hidden alt="No factory photo !!" width="60" height="60" /> : <div>Searching for photo...</div>} </div>*/}
              </ul>
            </nav>

            <Routes>
              <Route path="/" element={<Home env={userProfile.environment} />} />
              <Route path="/appInspection" element={<AppInspection />} />
              <Route path="/xDaysResult" element={<XDaysResult />} />
            </Routes>
          </Router>
        </div>
      </div>
    </div>
  )
}

export default App
