import React from "react"

const Home = props => {
  const { env } = props

  return (
    <div className="welcome">
      <div>
        {/* <img src="static/img/frontpageMap.jpg" aria-hidden alt="MMG Inspection Map"  />                 */}
        {env === "DEV" ? <iframe src="http://localhost:5000/map-marker" title="map" style={{ width: "100%", height: "103%", border: 0 }}></iframe> : <iframe src="https://mmgapp2.herokuapp.com/map-marker" title="map" style={{ width: "100%", height: "103%", border: 0 }}></iframe>}
      </div>
    </div>
  )
}
export default Home
