import React from "react"
import { usePromiseTracker } from "react-promise-tracker"
import Loader from "react-loader-spinner"

export const Spinner = props => {
  const { promiseInProgress } = usePromiseTracker({ area: props.area })
  const { spinnerMessage } = props

  return (
    promiseInProgress && (
      <div
        style={{
          marginTop: "0.5rem",
          width: "100%",
          height: "30",
          display: "flex",
          justifyContent: "center",
          alignItems: "center",
          color: "green",
          fontWeight: "bold"
        }}
      >
        {/* <Loader type="ThreeDots" color="#2BAD60" height="50" width="100" /> */}
        {/* <Loader type="Circles" color="rgba(129, 160, 250, 255)" height="40" width="100" /> */}
        {spinnerMessage}
        <Loader type="Circles" color="green" height="30" width="100" />
      </div>
    )
  )
}
