import { React, Fragment } from "react"

const ChangeTrack = props => {
  const { updateHistory, showHistory, setShowHistory, convertToLocal } = props

  var history = [...updateHistory]

  history.sort(function (a, b) {
    // note : must change the string back to date object for comparison
    if (Date.parse(a.updated_time) > Date.parse(b.updated_time)) return -1
    else return 1
  })

  let root = document.querySelector(":root")

  return (
    <Fragment>
      <div className="changeTrack_section_upper">
        <div>Change History</div>
        <div></div>
        <div>
          Show &nbsp;
          <i
            className="clickable fa fa-plus"
            onClick={() => {
              setShowHistory(true)
            }}
          ></i>
          &nbsp;&nbsp;Hide &nbsp;
          <i
            className="clickable fa fa-minus"
            onClick={() => {
              setShowHistory(false)
            }}
          ></i>
        </div>
        <div></div>
        <div>
          Expand &nbsp;
          <i
            className="clickable fa fa-plus"
            onClick={() => {
              //change class attribute dynamically
              root.style.setProperty("--changeTrack_max_height", "100rem")
            }}
          ></i>
          &nbsp;&nbsp; Collapse &nbsp;
          <i
            className="clickable fa fa-minus"
            onClick={() => {
              //change class attribute dynamically
              root.style.setProperty("--changeTrack_max_height", "4.3rem")
            }}
          ></i>
        </div>
        <div></div>
      </div>
      {showHistory ? (
        <div className="changeTrack_section_bottom">
          <div className="changeTrack_grid_header">
            <div>Local Time</div>
            <div>Updated By</div>
            <div>Mode</div>
            <div>Result Details</div>
            <div></div>
          </div>

          <div className="changeTrack_grid_outer_detail">
            {history.length > 0
              ? history.map(line => (
                  <div key={line.id} className="changeTrack_grid_detail">
                    <div>{convertToLocal(line.updated_time)}</div>
                    <div>{line.updated_by}</div>
                    <div>{line.updated_mode}</div>
                    <div>{line.misc}</div>
                  </div>
                ))
              : null}
          </div>
        </div>
      ) : null}
    </Fragment>
  )
}

export default ChangeTrack
