import React from "react"

const CheckList = props => {
  const { editMode, checkList } = props

  checkList.sort((a, b) => a.id - b.id) // must sort to present in order...

  const handleInputChange = (id, x) => {
    var selected = []
    if (editMode) {
      if (id === 88) {
        // Further Comments
        selected = ["Select", "Yes", "No", "Select"]
      } else {
        selected = ["Select", "OK", "NA", "X", "NI"]
      }

      let index = selected.findIndex(y => y === x.result)
      if (index === 4) {
        index = 1
      } else {
        index = index + 1
      }
      const nextSelected = selected[index]
      props.handleCheckItemChange(id, { ...x, result: nextSelected })
    }
  }

  return (
    <form
      onSubmit={event => {
        event.preventDefault() //  props.updateItem(item.id, item)
      }}
    >
      <div className="checkList_header">Basic Check List</div>
      <div className="checkFormGrid">
        <div>
          <table id="checkListTable">
            <thead>
              <tr>
                <th width="40%">Category</th>
                <th width="40%">Item</th>
                <th width="20%">Result</th>
                {/* <th>Drop Down Result</th> */}
              </tr>
            </thead>
            <tbody>
              {checkList
                ? checkList
                    .filter(y => y.id > 0 && y.id <= 42)
                    .map(x => (
                      <tr key={x.id}>
                        {x.category ? <td>{x.category}</td> : null}
                        {x.category ? (
                          <td>{x.item}</td>
                        ) : x.id === 0 ? (
                          <td className="checkCellSeparation" colSpan="3"></td>
                        ) : x.separator ? (
                          <td className="checkCellSeparationHeader" colSpan="3">
                            {x.separator}
                          </td>
                        ) : (
                          <td colSpan="2">{x.item}</td>
                        )}

                        {x.category || x.item ? (
                          <td
                            className="clickable checkCellResult"
                            onClick={() => {
                              handleInputChange(x.id, x)
                            }}
                          >
                            {x.result}
                          </td>
                        ) : null}
                      </tr>
                    ))
                : null}
            </tbody>
          </table>
        </div>

        <div className="checkFormGridCell">
          <table id="checkListTable" className="fixed">
            <thead>
              <tr>
                <th width="40%">Category</th>
                <th width="40%">Item</th>
                <th width="20%">Result</th>
                {/* <th>Drop Down Result</th> */}
              </tr>
            </thead>
            <tbody>
              {checkList
                ? checkList
                    .filter(y => y.id >= 43 && y.id <= 68)
                    .map(x => (
                      <tr key={x.id}>
                        {x.category ? <td>{x.category}</td> : null}
                        {x.category ? (
                          <td>{x.item}</td>
                        ) : x.id === 0 ? (
                          <td className="checkCellSeparation" colSpan="3"></td>
                        ) : x.separator ? (
                          <td className="checkCellSeparationHeader" colSpan="3">
                            {x.separator}
                          </td>
                        ) : (
                          <td colSpan="2">{x.item}</td>
                        )}

                        {x.category || x.item ? (
                          <td
                            className="clickable checkCellResult"
                            onClick={() => {
                              handleInputChange(x.id, x)
                            }}
                          >
                            {x.result}
                          </td>
                        ) : null}
                      </tr>
                    ))
                : null}
            </tbody>
          </table>
        </div>

        <div className="checkFormGridCell">
          <table id="checkListTable" className="fixed">
            <thead>
              <tr>
                <th width="40%">Category</th>
                <th width="40%">Item</th>
                <th width="20%">Result</th>
                {/* <th>Drop Down Result</th> */}
              </tr>
            </thead>
            <tbody>
              {checkList
                ? checkList
                    .filter(y => y.id >= 81)
                    .map(x => (
                      <tr key={x.id}>
                        {x.category ? <td>{x.category}</td> : null}
                        {x.category ? (
                          <td>{x.item}</td>
                        ) : x.id === 0 ? (
                          <td className="checkCellSeparation" colSpan="3"></td>
                        ) : x.separator ? (
                          <td className="checkCellSeparationHeader" colSpan="3">
                            {x.separator}
                          </td>
                        ) : (
                          <td colSpan="2">{x.item}</td>
                        )}

                        {x.category || x.item ? (
                          <td
                            // bgcolor="white"
                            className="clickable checkCellResult"
                            onClick={() => {
                              handleInputChange(x.id, x)
                            }}
                          >
                            {x.result}
                          </td>
                        ) : null}
                      </tr>
                    ))
                : null}
            </tbody>
          </table>
        </div>
      </div>
    </form>
  )
}

export default CheckList
