import React, { Fragment } from "react"

const MainMenu = props => {
  const { handleSave, handleDelete, handleSearch, inspectionID, printReport, editMode, setEditMode, userProfile, misc, showFull, clearScreen, updateHistory, setDisplayAlert, oldMC } = props

  const submitted = misc.submitted
  const editable = userProfile.ignore_submit === undefined ? false : userProfile.ignore_submit

  const handleClick = event => {
    const id = event.target.id

    setEditMode(false)

    if (id === "save") {
      submitted ? handleSave(true) : handleSave(false)
    }

    if (id === "edit") {
      // oldMC doesn't allow edit
      if (oldMC.current) {
        setDisplayAlert({ message: "You can NOT edit inspecton with obsolete MC.  This is for view only !", position: 0, timeout: 2500, color: "alert__red" })
        return
      }
      setEditMode(true)
    }

    if (id === "delete") {
      handleDelete(inspectionID)
    }
    if (id === "search") {
      setEditMode(false)
      handleSearch(inspectionID)
    }
    if (id === "print") {
      printReport("Inspection_Report.xlsx", false)
    }
    if (id === "print_draft") {
      // print draft watermark
      printReport("Inspection_Report.xlsx", true)
    }

    if (id === "cancel") {
      setEditMode(false)
      clearScreen()
    }
  }
  return (
    <Fragment>
      <div className="mainMenu">
        <div className="mainMenu_inspectionKey">
          Inspection Key : &nbsp;&nbsp;
          {/* {(submitted || "") && showFull ? <i className="fa fa-lock mainMenu_lock"></i> : null} */}
          {(submitted || "") && showFull ? (
            <i
              className="fa fa-lock mainMenu_lock clickable"
              onClick={() => {
                if (editable) {
                  var confirm = window.confirm("Are you sure you want to unlock this inspeciton ? ")
                  if (confirm === true) {
                    handleSave(false)
                  }
                } else {
                  alert("You have no rights to unlock this inspeciton !")
                }
              }}
            ></i>
          ) : null}
          {(!submitted || "") && showFull ? (
            <i
              className="fa fa-unlock mainMenu_unlock clickable"
              onClick={() => {
                var confirm = window.confirm("Are you sure you want to submit or lock this inspeciton ? ")
                if (confirm === true) {
                  handleSave(true)
                }
              }}
            ></i>
          ) : null}
        </div>
        {editMode || "" ? (
          <button id="save" onClick={handleClick}>
            Save
          </button>
        ) : null}
        {!editMode ? (
          <button id="search" onClick={handleClick}>
            Search
          </button>
        ) : null}
        {!editMode && showFull && (!submitted || editable) ? (
          <button id="edit" onClick={handleClick}>
            Edit&nbsp;
            {(submitted || "") && showFull ? <i className="fa fa-lock"></i> : null}
          </button>
        ) : null}
        {showFull && (!submitted || editable) ? (
          // {editMode ? (
          <button id="cancel" onClick={handleClick}>
            Cancel
          </button>
        ) : null}
        {!editMode && submitted ? (
          <button id="print" onClick={handleClick}>
            Print
          </button>
        ) : null}
        {!editMode && !submitted && showFull ? (
          <button id="print_draft" onClick={handleClick}>
            Print Draft
          </button>
        ) : null}

        {(showFull || "") && editable && showFull && updateHistory.length > 0 ? (
          <button id="delete" onClick={handleClick} className="deleteButton">
            Delete
          </button>
        ) : null}
      </div>
    </Fragment>
  )
}

export default MainMenu
